const TOGGLE_CHAT = "hangperson/chat-screen/TOGGLE"

export default (state = false, action) => {
  if (action.type === TOGGLE_CHAT){
    return !state;
  }

  return state;
};

export const toggleChat = () => {
  return {
    type: TOGGLE_CHAT
  };
};



